@font-face {
  font-family: 'TT Travels';
  src: url("../fonts/TTTravels-Light.eot");
  src: local("TT Travels Light"), local("TTTravels-Light"), url("../fonts/TTTravels-Light.woff") format("woff"), url("../fonts/TTTravels-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'TT Travels';
  src: url("../fonts/TTTravels-Regular.eot");
  src: local("TT Travels Regular"), local("TTTravels-Regular"), url("../fonts/TTTravels-Regular.woff") format("woff"), url("../fonts/TTTravels-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'TT Travels';
  src: url("../fonts/TTTravels-Medium.eot");
  src: local("TT Travels Medium"), local("TTTravels-Medium"), url("../fonts/TTTravels-Medium.woff") format("woff"), url("../fonts/TTTravels-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'TT Travels';
  src: url("../fonts/TTTravels-DemiBold.eot");
  src: local("TT Travels DemiBold"), local("TTTravels-DemiBold"), url("../fonts/TTTravels-DemiBold.woff") format("woff"), url("../fonts/TTTravels-DemiBold.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

body {
  font-family: "TT Travels", "Arial", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

html {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

/* :focus {
  outline: none;
} */
input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type='search']::-webkit-search-decoration,
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-results-button,
input[type='search']::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type='number'] {
  appearance: textfield; }

select,
textarea,
input:matches([type='email'], [type='number'], [type='password'], [type='search'], [type='tel'], [type='text'], [type='url']) {
  appearance: none; }

button {
  display: block;
  background-color: transparent;
  border: 0; }

a {
  text-decoration: none; }

:root {
  font-size: 14px; }

html {
  scrollbar-width: none;
  scroll-behavior: smooth; }
  html::-webkit-scrollbar {
    width: 0; }
  html::-webkit-scrollbar-thumb {
    border: 0 solid transparent; }

body {
  display: flex;
  flex-direction: column;
  overscroll-behavior: contain;
  transition: transform 1s ease; }

::selection {
  color: #ffffff;
  background-color: #000000; }

a,
button {
  cursor: pointer; }
  a:focus,
  button:focus {
    outline: none; }

.is-scroll-disabled-true {
  overflow: hidden; }

svg {
  transition: color 0.1s ease; }

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.15s ease; }
  @media (hover: hover) {
    .button:active {
      transform: scale(0.98); } }

.container--xxl, .container--xl, .container--m, .container--s {
  margin-right: auto;
  margin-left: auto;
  padding-right: Max(15px, env(safe-area-inset-right));
  padding-left: Max(15px, env(safe-area-inset-left)); }

.container--xxl {
  max-width: 1280px; }

.container--xl {
  max-width: 1115px; }

.container--m {
  max-width: 1070px; }

.container--s {
  max-width: 966px; }

.noscript-popup {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  text-align: center;
  background-color: #000000; }
  .noscript-popup h1 {
    color: #ffffff; }

.page {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  background-color: #ffffff; }
  .page__content {
    width: 100%; }
